.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
  }
  
  .Modal {
    max-width: calc(100vw - 48px);
    max-height: calc(100vh - 24px);
  }
  
  .CloseModal{
    all:unset;
    position:absolute;
    top:50px;
    right: 50px;
    padding: 20px;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
  
  }